@import url(https://fonts.googleapis.com/css?family=Charm|Damion);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bitter:ital,wght@1,100;1,300;1,600&display=swap);
.container-fluid {
  margin: 0 !important;
  padding: 0 !important;
}

.navbar {
  background-color: #fff;
  font-family: 'Bitter', serif;
  font-weight: 300;
}

@font-face {
  font-family: 'Decovar Regular24';
  src: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/209981/Decovar-VF.ttf');
}

.nav-link {
  color: #000 !important;

}
.projectsWrapper .row {
    margin: 0 auto;
    width: 80%;
    margin-top: 2em;
}

.projectsWrapper h4 {
    font-family: 'Bitter', serif;
    font-weight: bolder;
    color: #000;
    text-transform: uppercase;
    text-align: center;
    font-size: 2.3rem;
}

.card {
    transition: all 0.3s linear;
    margin-bottom: 50px;
    background-color: #272D2D;
    height: 300px;
    display: flex;
    flex-direction: column;
    flex: 1 1;
    font-family: 'Poppins', sans-serif !important;
    font-weight: 400;
    max-width: 400px;
}

.card-header {
    font-weight: 500;
}

.card-footer {
    border-top: transparent;
    transition: all 0.3s linear;
    margin-top: auto;
}


.card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.card-text {
    font-weight: 200;
}

.card:hover {
    border: 0.04rem solid rgba(0, 0, 0, 0.4);
    box-shadow: 2px 2px 2px 5px 0 rgba(0, 0, 0, 0.2);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.card:hover .project-title {
    color: #eecc3a;
}

.project-title {
    font-weight: bold;
    color: #000;
    text-transform: uppercase;
    display: block;
}

.card-footer i {
    color: black !important;
    font-size: 1.2rem !important;
    padding-left: 20px;
}

.card-footer p {
    display: inline-block;
    font-size: 1.4rem;
}

.card-footer a {
    float: right;
    font-weight: 900;
}
.main {
    background-color: #fff;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    margin-top: 2em;
    margin-bottom: 3em;
    height: 85vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.title-name {
    margin: 0;
    font-size: 5vw;
    background-size: 30%;
    background-repeat: repeat;
    letter-spacing: 0.4rem;
    font-weight: 600;
    color: #000;
    font-family: 'Bitter', serif;
    text-decoration: underline;
}

.profession-header h3 {
    font-size: 2vw;
    /* color: #000; */
    color: #000;
    font-weight: 300;
    font-family: 'Bitter', serif;
}

.tagline {
    font-weight: 300;
    color: #000;
    font-size: 2vw;
    font-family: 'Bitter', serif;
}

hr {
    width: 80%;

}

.socials i {
    color: rgb(180, 177, 177);
    font-size: 2em;
    text-align: center;
}

.socialIcons {
    padding-left: 2em;
}

.row:nth-child(4),
.row:nth-child(3) {
    margin-top: 20px;
}

@media (max-width: 600px) {
    .main {
        padding-left: 20px;
    }

    i {
        font-size: 32px;
    }

    .title-name {
        font-size: 10vw;
    }

    .profession-header h3 {
        font-size: 7vw;
    }

    .tagline {
        font-size: 6vw;
    }

    .row:nth-child(3) {
        margin-top: 10px;
    }

}
.blogsWrapper {
    width: 80%;
    margin: 0 auto;
}

.blogsWrapper .row {
    margin-top: 70px;
    padding: 0 !important;
}

.blogsWrapper h3 {
    font-family: 'Bitter', serif;
    font-weight: bolder;
    text-transform: uppercase;
    text-align: center;
    margin-top: 20px;
    font-size: 3rem;
}

.blogsWrapper li {
    list-style: none;
}
